* {
	box-sizing: border-box;
	margin: 0;
	padding: 0;
}

.app {
	width: 100%;

	display: flex;
	justify-content: center;
}

.todo {
	width: 700px;
	max-width: 100%;
}

.todo__title {
	margin-bottom: 5px;
}

.todo__sub-title {
	font-size: 18px;
	color: skyblue;

	margin-bottom: 25px;
}

.todo__todo-table {
	width: 100%;
	margin-top: 15px;
	border-collapse: collapse;
}

.todo-table__header {
	border-bottom: 2px solid #000;
}

.todo-table__content .content__item {
	height: 40px;
	border-bottom: 1px solid #eee;
}

.todo-table__content .content__item td {
	text-align: center;
}

.todo-table__content .item__remove-btn {
	border: none;
	outline: none;
	color: red;
	background: inherit;
	cursor: pointer;
	text-decoration: underline;
}

/* Status component style */
.status {
	cursor: pointer;
}

/* Add task component style */
.add-task {
	width: 100px;
	height: auto;
	padding: 10px;
	border: 1px solid #eee;

	display: flex;
	justify-content: center;
	align-items: center;

	cursor: pointer;
}

.add-task__form {
	position: absolute;
	top: 50%;
	left: 50%;
	transform: translate(-50%, -50%);

	width: 340px;
	height: 430px;
	padding: 20px;
	background: #eee;
	border: 1px solid #000;

	display: flex;
	flex-direction: column;
}

.add-task__form > * {
	margin-bottom: 20px;
}

.add-task__form [class$="-btn"] {
	padding: 15px;
	cursor: pointer;
}
